/* === Helpers === */

.circle	{
	border-radius: 50%;
}

.center-align	{
	margin-left: auto;
	margin-right: auto;
	display: block;
}

.space-default	{
	margin-bottom: $space / 2;

	@include breakpoint($small)	{
		margin-bottom: $space;		
	}
}

.space-double	{
	margin-bottom: $space;

	@include breakpoint($small)	{
		margin-bottom: $space * 2;		
	}	
}

.space-triple	{
	margin-bottom: $space * 1.5;

	@include breakpoint($small)	{
		margin-bottom: $space * 3;		
	}	
}

.space-quad	{
	margin-bottom: $space * 3;

	@include breakpoint($small)	{
		margin-bottom: $space * 4;		
	}	
}

.space-cinco	{
	margin-bottom: $space * 4;

	@include breakpoint($small)	{
		margin-bottom: $space * 5;		
	}	
}

.clearfix	{
	width: 100%;
	float: left;
	clear: both;
	height: 0px;

	&:before, &:after	{
		display: table;
		content: "";
		width: 100%;
		float: left;
	}
}

.wrapper	{
	background-image: -webkit-linear-gradient(#FFFFFF 30%, #F8F8F8 100%);
	background-image: -o-linear-gradient(#FFFFFF 30%, #F8F8F8 100%);
	background-image: linear-gradient(#FFFFFF 30%, #F8F8F8 100%);
	box-shadow: 0px 4px 4px 0px rgba(0,0,0,0.06);
	padding: $space;
	float: left;
	width: 100%;
}

.aqua-gradient	{
	background-image: -webkit-linear-gradient(bottom, #50C9C3 0%, #96DEDA 100%);
	background-image: -o-linear-gradient(bottom, #50C9C3 0%, #96DEDA 100%);
	background-image: linear-gradient(to top, #50C9C3 0%, #96DEDA 100%);
	background-color: #96DEDA;
	box-shadow: 0px 2px 0px 0px #3EA49F;
	color: $dark-grey;		
	border-bottom: none;
	text-decoration: none;
	transition: all 0.3s ease-out;

	&:hover	{
		background: #50C9C3;
		box-shadow: 0px 2px 0px 0px transparent;
		transition: all 0.3s ease-in;
	}	
}










